import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import api from '../services/api';
import { setCampaigns } from '../store/slices/campaignSlice';
import {
  Box,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  Chip,
  Snackbar,
  Alert
} from '@mui/material';
import { Add as AddIcon } from '@mui/icons-material';

function Campaigns() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { campaigns } = useSelector((state) => state.campaigns);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });
  const [loadingId, setLoadingId] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchCampaigns = async () => {
      try {
        setLoading(true);
        const res = await api.get('/campaigns');
        dispatch(setCampaigns(res.data.campaigns || []));
      } catch (err) {
        setSnackbar({ open: true, message: 'Failed to load campaigns', severity: 'error' });
      } finally {
        setLoading(false);
      }
    };
    fetchCampaigns();
  }, [dispatch]);

  const getStatusColor = (status) => {
    switch (status) {
      case 'active':
        return 'success';
      case 'scheduled':
        return 'info';
      case 'completed':
        return 'default';
      default:
        return 'default';
    }
  };

  const handleDeliver = async (campaign) => {
    setLoadingId(campaign._id || campaign.id);
    try {
      const res = await api.post(`/campaigns/${campaign._id || campaign.id}/deliver`);
      setSnackbar({ open: true, message: `Delivery started for ${res.data.total} customers!`, severity: 'success' });
    } catch (err) {
      setSnackbar({ open: true, message: 'Failed to start delivery', severity: 'error' });
    } finally {
      setLoadingId(null);
    }
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
        <Typography variant="h4" component="h1">
          Campaigns
        </Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={() => navigate('/campaigns/create')}
        >
          Create Campaign
        </Button>
      </Box>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell>Type</TableCell>
              <TableCell>Status</TableCell>
              <TableCell>Start Date</TableCell>
              <TableCell>End Date</TableCell>
              <TableCell>Target Audience</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {loading ? (
              <TableRow>
                <TableCell colSpan={7} align="center">Loading...</TableCell>
              </TableRow>
            ) : campaigns.length === 0 ? (
              <TableRow>
                <TableCell colSpan={7} align="center">No campaigns found.</TableCell>
              </TableRow>
            ) : campaigns.map((campaign) => (
              <TableRow key={campaign._id || campaign.id}>
                <TableCell>{campaign.name}</TableCell>
                <TableCell>{campaign.type}</TableCell>
                <TableCell>
                  <Chip
                    label={campaign.status}
                    color={getStatusColor(campaign.status)}
                    size="small"
                  />
                </TableCell>
                <TableCell>{campaign.startDate || (campaign.schedule && campaign.schedule.startDate)}</TableCell>
                <TableCell>{campaign.endDate || (campaign.schedule && campaign.schedule.endDate)}</TableCell>
                <TableCell>{campaign.targetAudience || '-'}</TableCell>
                <TableCell>
                  <Button
                    size="small"
                    onClick={() => navigate(`/campaigns/${campaign._id || campaign.id}`)}
                  >
                    View Details
                  </Button>
                  <Button
                    size="small"
                    variant="outlined"
                    color="secondary"
                    sx={{ ml: 1 }}
                    onClick={() => handleDeliver(campaign)}
                    disabled={loadingId === (campaign._id || campaign.id)}
                  >
                    {loadingId === (campaign._id || campaign.id) ? 'Delivering...' : 'Deliver'}
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Snackbar
        open={snackbar.open}
        autoHideDuration={4000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert severity={snackbar.severity} onClose={() => setSnackbar({ ...snackbar, open: false })}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
}

export default Campaigns; 