import React, { useEffect, useState } from 'react';
import { Box, Typography, Grid, Paper, Avatar } from '@mui/material';
import PeopleIcon from '@mui/icons-material/People';
import CampaignIcon from '@mui/icons-material/Campaign';
import TrendingUpIcon from '@mui/icons-material/TrendingUp';

const statsConfig = [
  {
    label: 'Total Customers',
    icon: <PeopleIcon fontSize="large" color="primary" />,
    color: '#1976d2',
    key: 'customers',
    value: 2, // match mock data
  },
  {
    label: 'Active Campaigns',
    icon: <CampaignIcon fontSize="large" color="secondary" />,
    color: '#ff9800',
    key: 'campaigns',
    value: 1, // match mock data
  },
  {
    label: 'Campaign Reach',
    icon: <TrendingUpIcon fontSize="large" color="success" />,
    color: '#43a047',
    key: 'reach',
    value: 1000, // match mock data
  },
];

function AnimatedNumber({ value }) {
  const [display, setDisplay] = useState(0);
  useEffect(() => {
    let start = 0;
    const duration = 800;
    const step = Math.ceil(value / 30);
    const interval = setInterval(() => {
      start += step;
      if (start >= value) {
        setDisplay(value);
        clearInterval(interval);
      } else {
        setDisplay(start);
      }
    }, duration / 30);
    return () => clearInterval(interval);
  }, [value]);
  return <span>{display}</span>;
}

function Dashboard() {
  return (
    <Box sx={{ flexGrow: 1 }}>
      <Typography variant="h4" component="h1" gutterBottom sx={{ color: '#fff', fontWeight: 700, textShadow: '0 2px 8px rgba(0,0,0,0.18)' }}>
        Dashboard
      </Typography>
      <Grid container spacing={3}>
        {statsConfig.map((stat) => (
          <Grid item xs={12} md={4} key={stat.key}>
            <Paper
              sx={{
                p: 3,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                height: 170,
                borderRadius: 3,
                boxShadow: '0 4px 24px rgba(25, 118, 210, 0.10)',
                transition: 'transform 0.18s, box-shadow 0.18s',
                cursor: 'pointer',
                '&:hover': {
                  transform: 'translateY(-6px) scale(1.03)',
                  boxShadow: `0 8px 32px ${stat.color}33`,
                },
                background: 'rgba(255,255,255,0.95)',
              }}
              elevation={4}
            >
              <Avatar sx={{ bgcolor: stat.color, mb: 1, width: 56, height: 56 }}>
                {stat.icon}
              </Avatar>
              <Typography component="h2" variant="h6" color="text.secondary" gutterBottom sx={{ fontWeight: 600 }}>
                {stat.label}
              </Typography>
              <Typography component="p" variant="h4" sx={{ fontWeight: 700, color: stat.color }}>
                <AnimatedNumber value={stat.value} />
              </Typography>
            </Paper>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
}

export default Dashboard; 